<template>
    <div class="chat-container">
        <div :class="['bubble-container', getFlexDirection]">
            <v-menu absolute offset-y>
                <template v-slot:activator="{ on, attr }">
                    <div v-on="on" class="order-2">
                        <span v-show="message.hover" class="pa-1" style="height: 100%; margin: 0 auto">...</span>
                    </div>
                </template>
                <v-list>
                    <v-list-item @click="moreInfo">
                        <v-list-item-title>
                            More info
                        </v-list-item-title>
                    </v-list-item>
                    <v-list-item v-if="isMessageDeletable" @click="deleteMessage">
                        <v-list-item-title>
                            Delete Message
                        </v-list-item-title>
                    </v-list-item>
                </v-list>
            </v-menu>

            <div :class="['bubble-body-container', getColor, 'pa-2', 'order-1']" @click="readMessage()">
                <div class="text-body-1">{{ message.text }}</div>
                <div class="text-caption text-right">{{ timeSince }}</div>
                <div class="bubble-id" style="display: none">{{ message.id }}</div>
            </div>
        </div>

        <div :class="['bubble-last-read', getFlexDirection, 'pr-2']">
            <slot name="lastReadIcon"></slot>
        </div>
    </div>
</template>

<script>
    import { mapState } from 'vuex'

    export default {
        data() {
            return {

            }
        },
        props: {
            message: {
                type: Object,
                required: true,
            },
            currentTime: {
                type: Number,
                required: false,
            },
            owner: {
                type: Boolean,
                default: false,
            },
            lastMessage: {
                type: Boolean,
                default: false,
            }
        },
        computed: {
            ...mapState({
                user: state => state.auth.user,
                activeModules: state => state.auth.activeModules,
            }),
            timeSince() {
                if (typeof this.currentTime !== 'number' || typeof this.message.timestamp !== 'number') {
                    return '';
                }

                let seconds = this.currentTime - this.message.timestamp;
                if (seconds < 0) {
                    return '';
                }
                if (seconds < 60) {
                    return `${Math.trunc(seconds)}s`;
                }

                let minutes = seconds / 60;
                if (minutes < 60) {
                    return `${Math.trunc(minutes)}m`;
                }

                let hours = minutes / 60;
                if (hours < 24) {
                    return `${Math.trunc(hours)}h`;
                }

                let days = hours / 24;
                return `${Math.trunc(days)}d`;
            },
            isMessageDeletable() {
                if (this.owner === true) {
                    const chatModule = this.activeModules.find(x => x.id === 'chat');
                    if (typeof chatModule !== 'undefined') {
                        const feature = chatModule.userModuleFeatures.find(x => x.moduleFeatureId === 'chat-delete-message')
                        if (typeof feature !== 'undefined') {
                            return true;
                        }
                    }
                }

                return false;
            },
            getFlexDirection() {
                if (this.owner) {
                    return 'flex-right';
                }
                else {
                    return 'flex-left';
                }
            },
            getColor() {
                if (this.message.highlighted === true) {
                    return 'highlight'
                }
                else {
                    if (this.message.color === null) {
                        if (this.message.userId === this.user.id) {
                            return 'blue';
                        }
                        else {
                            return 'green';
                        }
                    }
                    else {
                        return this.message.color;
                    }
                }
            }
        },
        watch: {
            message: {
                handler(newValue) {
                    if (typeof newValue !== 'undefined' && newValue.highlighted === true) {
                        this.$_console_log('Message.Highlighted watcher: highlight = true');
                        setTimeout(() => {
                            this.$store.dispatch('highlightMessage', { messageId: newValue.id, conversationId: newValue.conversationId, on: false });
                        }, 6000);
                    }
                },
                deep: true
            }
        },
        methods: {
            deleteMessage() {
                this.$emit('deleteMessage', this.message.id);
                this.optionDialog = false;
            },
            moreInfo() {
                this.$emit('moreInfo', this.message);
                this.optionDialog = false;
            },
            isMessageReadable() {
                // Not self
                if (this.user.id !== this.message.userId) {
                    // No array or array length is 0
                    if (!Array.isArray(this.message.readReceipts) || (Array.isArray(this.message.readReceipts) && this.message.readReceipts.length === 0)) {
                        return true;
                    }
                    // No read receipts from self
                    else if (typeof this.message.readReceipts.find(x => x.userId === this.user.id) === 'undefined') {
                        return true;
                    }
                }

                return false;
            },
            readMessage() {
                if (!this.isMessageReadable()) {
                    this.$_console_log('ReadMessage: Message is not readable');
                    return;
                }

                this.$store.dispatch('readChatMessage', { conversationId: this.message.conversationId, messageId: this.message.id });
            },
        }
    }
</script>

<style scoped>
    .chat-container {
        display: flex;
        flex-direction: column;
    }

    .bubble-container {
        display: flex;
        flex-direction: row;
        max-width: 100%;
    }

    .bubble-body-container {
        border: 1px solid gray;
        border-radius: 15px;
        display: flex;
        flex-direction: column;
        max-width: 70%;
    }

    .bubble-last-read {
        display: flex;
        flex-direction: row;
    }

    .flex-left {
        flex-direction: row;
    }

    .flex-right {
        flex-direction: row-reverse;
    }

    .order-1 {
        order: 1;
    }

    .order-2 {
        order: 2;
    }

    .green {
        background-color: green;
    }

    .blue {
        background-color: blue;
    }

    .highlight {
        /* Get this to shift the color somehow */
        background-color: aquamarine;
    }
</style>
